/*
 * Table Styles
 * 
 * DataTable and standard Table style overrides.
 */

table.dataTable {
    border-bottom: 1px solid #e3e3e3;
    word-wrap: break-word;
    word-break: break-word;
    white-space: normal;
    thead {

        tr th {
            background-color: #f5f5f5;
            border-color: #e3e3e3;
            font-size: .9em;
            padding: 8px 4px 4px;

            /* Sorting Icon */
            &.sorting,
            &.sorting_asc, 
            &.sorting_desc, 
            &.sorting_asc_disabled, 
            &.sorting_desc_disabled {
                padding-right: 25px;
                &:after {
                    bottom: 4px;
                    right: 8px;
                }
            }
            /* End: Sorting Icon */
        }
    }
    tbody {
        tr {
            /* Responsive Controls */
            td.responsive-control i.fa:before {
                content: "\f105";
            }
            &.parent td.responsive-control i.fa:before {
                content: "\f107";
            }

            /* Responsive sub table */
            &.child {
                padding: 0;
                border-left: 2px solid #e3e3e3;
                border-right: 2px solid #e3e3e3;
                td.child {
                    padding: 0;
                    table.sub-table {
                        margin: 0;
                    }
                }
            }
            td {
                font-size: .9em;
                padding: 4px; // Thin out normal bootstrap padding
            }
        }
    }
}

.dataTables_info {
    font-size: .8em;
}
.dataTables_length {
    font-size: .8em;
}
body:not(.admin) {
    .dataTables_length select {
        color: #fff;
        background-color: @medium-brown;
        border-color: lighten(@medium-brown, 15%);
    }
}
table, table.dataTable {
    &.table-dark,  
    &.table-dark table.sub-table {
        border-color: darken(@brick-red, 5%);
        tr {
            border-color: darken(@brick-red, 5%);

            th {
                background-color: @brick-red;
                color: #fff;
                border-bottom-color: lighten(@brick-red, 15%);
                border-top-color: darken(@brick-red, 5%);
                border-left-color: darken(@brick-red, 5%);
                border-right-color: darken(@brick-red, 5%);
            }

            td {
                border-color: darken(@brick-red, 5%);
                
                /* Responsive sub table */
                &.child {
                    border-left: 2px solid darken(@brick-red, 5%);
                    border-right: 2px solid darken(@brick-red, 5%);
                }
            }
        }

        &.table-striped {
            >thead {
                th {
                    background-color: @brick-red;
                    color: #fff;
                    border-bottom-color: lighten(@brick-red, 15%);
                    border-top-color: darken(@brick-red, 5%);
                    border-left-color: darken(@brick-red, 5%);
                    border-right-color: darken(@brick-red, 5%);
                }
            }
            >tbody {
                background-color: lighten(@brick-red, 1%);
                &>tr:nth-of-type(odd) {
                    background-color: darken(@brick-red, 2%);
                }
            }
        }
    }
}